@import './globals-fonts/globals-fonts.scss';

body {
    margin: 0;
}
ul, li {
    display: block;
    margin: 0;
    padding: 0;
}
* a {
    text-decoration: none;
}
* {
    box-sizing: border-box;
}
.container {
    width: 1150px;
    padding: 0 20px;
    margin: 0 auto;
}
.active {
    color: $color-blue;
}