@import '../../globals-fonts/globals-fonts.scss';

.header {
    padding: 45px 0;
    display: flex;
    align-items: center;
    .reporter {
        font-family: 'Proxima Nova Bold';
        font-size: 25px;
        line-height: 30px;
        color: $color-black;
    }
    .menu {
        font-family: 'Proxima Nova Regular';
        margin-left: auto;
        .menuItems {
            display: flex;
            font-size: 18px;
            line-height: 22px;
            .menuItem {
                a {
                    color: $color-black;
                }
                &:hover a {
                    color: $color-blue;
                }
                
                .news {
                    margin: 0 35px;
                }
            }
            .active a {
                color: $color-blue;
            }
        }
    }
}

